import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';


import { PublicLayoutPageComponent } from './public-layout-page/public-layout-page.component';
import { HomePageComponent } from './home-page/home-page.component';
import { DetailsPageComponent } from './details-page/details-page.component';

import { SearchResultsPageComponent } from './search-results-page/search-results-page.component';
import { LoginPageComponent } from './login-page/login-page.component';
import { InscriptionPageComponent } from './inscription-page/inscription-page.component';
import { ContactComponent } from './contact/contact.component';
import { ForgotPasswordComponent } from './forgot-password/forgot-password.component';

const routes: Routes = [
    { path: '',
      component: PublicLayoutPageComponent,
                children: [
                    { path: '',    component: HomePageComponent },
                    { path: 'recherche',    component: SearchResultsPageComponent },
                    { path: 'infos',    component: DetailsPageComponent },
                    { path: 'login',    component: LoginPageComponent },
                    { path: 'inscription',    component: InscriptionPageComponent },
                    { path: 'contact',    component: ContactComponent },
                    { path: 'reset',    component: ForgotPasswordComponent }
                    //{ path: ':id', component: CustomersDetailComponent }
                ]
    }
  ];
  
@NgModule({
  imports: [ RouterModule.forChild(routes) ],
  exports: [ RouterModule ]
})

export class PublicPagesRoutingModule { }